body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-div {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin: 15px 40px;
    padding: 0;
    -webkit-align-items: center;
            align-items: center;
}

.logo {
   margin: 0;
   padding: 0;
}

.logo-img {
    margin-top: 12px;
    width: 60px;
}

.nav-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end; 
    /* margin-right: 20px;  */
}

.nav-list {
    font-family: 'Montserrat', sans-serif;
    font-size: 1.3em;
    font-weight: 500;
    text-decoration: none;
    color: #2a2c37;
    margin: 10px 20px;
    cursor: pointer;
    position: relative;
}
.nav-list:last-child {
    margin-right: 0;
}

.hover-line {
    position : absolute;
    bottom: -5px;
    left: 0;
    height: 2px;
    width: 0%;
    transition: width 0.4s linear;
}


.nav-list:hover .hover-line {
    background-color: #46fff6; 
    width: 24px;
}

.nav-container .is-active {
    font-weight: 700;
    color: #7f00e0;

}

.text-under-name {
    font-family: 'Noto Sans', sans-serif;
    font-size: 2em;
    margin: 1% 0;
}
.social-handle {
    position: fixed;
    padding: 0;
    margin-left: 15px;
}


.social-icons li h1 {
    color: black;
    transition: color 0.5s;
}

.social-icons li h1:visited {
   color: black;
}

.social-icons li h1:hover {
    color: #46fff6;
}
/* body {
    margin: 0;
    padding: 0;
} */

.main-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin: 24%;
    margin-top: 9%;
    
}

.text-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: left;
    margin-top: 10%;
}


.my-name{
    font-family: 'Open Sans', sans-serif;
    font-size: 8.2em;
    margin: 0;
    margin-left: -6px;
    font-weight: 800;
    letter-spacing: 1px;

}


.text-wrapper h3 {
    font-family: 'Open Sans', sans-serif;
    font-size: 2.8em;
    margin: 0;
    color: #696b72;
}

.front-end {
    border-bottom: 2px solid black;
}

.text-wrapper h5{ 
    font-family: 'Open Sans', sans-serif;
    font-size: 1.4em;
    font-weight: 400;
    margin-top: 7%;
}

.btn-container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    margin-top: 2%;
}

button {
    width: 180px;
    height: 60px;
    margin: 10px;
    border-radius: 10px;
    font-size: 1.3em;
    font-weight: 500;
    background-color: white;
    color: black;
    cursor: pointer;
    border: none;
    border: 2px solid #7f00e0; 
    transition: all 0.4s ease;
    font-family: 'Heebo', sans-serif;
    letter-spacing: 0.5px;
    box-shadow: 3px 7px 10px rgba(0, 0, 0, 0.1); 
    /* box-shadow: 3px 6px white, 3px 6px 1px 1px #7f00e0;  */
}  

button .btnForEmail {
  text-decoration: none;
  color: black;
}

button:nth-child(1) {
   margin-left: 0;
}

button:focus { outline:0 !important; }

button:hover {
  box-shadow: 3px 7px 10px rgba(70, 255, 246, 0.9);
  background-color: #7f00e0;
  color: white;
  font-weight: 800;
  -webkit-transform: translateY(-4px);
          transform: translateY(-4px);
}
  
button:hover .btnForEmail{
  color: white;
}

button:active {
    -webkit-transform: scale(.98);
            transform: scale(.98);
}
