.about-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 7% 10%;
}


.about-me-text-container {
    text-align: left;
    margin: 0 auto;
}

.about-me {
    margin: 0 auto;
    width: 74%;
}
.about-me h1 {
    text-align: left;
}


.profile-top {
    margin: 0 auto;
    height: 350px;
    padding: 0 6% 4% 6%;
}
.profile-card {
   width: 620px;
   height: 300px;  
}

.profile-card-text {
    text-align: left;
}
.profile-card-text h1 {
    font-size: 2.4em;
    letter-spacing: 1px;  
    margin-top: 0;
}
.profile-card-text p {
    margin: 10px 0;
}

.profile-box {
    position: absolute;
     margin: 0 auto;
    width: 620px;
    height: 300px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: #e7e2ff;
    border-radius: 14px;
    -webkit-align-items: center;
            align-items: center;
    border: 4px solid white;
    box-shadow:  4px 6px 9px #adadb2;
}

.profile-contact-btn {
    position: relative;
    width: 200px;
    height: 54px;
    bottom: -274px;
    left: 386px;
    background-color: #6034e5;
    border-radius: 30px;
    border: 3px solid white;
    display: -webkit-flex;
    display: flex;
    box-shadow:  4px 6px 9px #adadb2; 
    
}

.profile-btn-text {
    margin: auto;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    color: white;
    text-transform: uppercase;
    font-size: 1.2em;
    letter-spacing: 1.2px;
    text-shadow: 1px 1px #232226;
}

.btnForEmail {
    text-decoration: none;
}
.profile-contact-btn:active {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
}


.profile-pic {
    width: 220px;
    height: 220px;
    border-radius: 50%;  
    border: 6px solid white;
    margin-right: 4.5%;
}


.about-me-text-container {
    width: 74%;
}

.about-me-text {
    font-family: 'Roboto', sans-serif;
    font-size: 1.5em;
    font-weight: 400;
    line-height: 1.7em;
    margin: 24px 0;
    color: #2a2a2b;
    letter-spacing: 0.2px;
}

.about-me-text span {
    display: -webkit-flex;
    display: flex;
}

.about-me-text.tech {
    margin-bottom: 0;
}

.tech-list {
    margin: 20px;
    padding-left: 5px;
}
li.tech-use {
    list-style: none;
    font-family: 'Heebo', sans-serif;
    font-weight: 500;
    font-size: 1.6em;
    line-height: 1.6em;

}

.laptop-img, .explore-img {
    width: 360px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin: auto;
    padding: 5%;
}

