    margin-left: 15px;
  
} */

.project-cards {
    font-family: 'Heebo', sans-serif;
    display: -webkit-flex;
    display: flex;
    margin: 10% 0;
}

.project-cards:nth-child(1) {
    margin-top: 2%;
}

.project-preview {
    width: 400px;
    margin-right: 10%;
}
.project-preview img {
    width: 100%;
    object-fit: cover;
    /* overflow: hidden; */
}

.project-info {
