    height: 34px;
    font-size: 1.2em; 
    border-radius: 20px;
    
    /* color: black; */
    /* border: 2px solid #7f00e0; */
}

.btn-text {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
    text-decoration: none;
    font-size: 0.8em;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin: 0 auto;
    padding: 0;
